.hover-pane {
  position: relative;
}
.hover-pane .hover-action-link {
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
  border: solid 1px #888;
  border-radius: 2px;
  background: #999;
  color: #fff;
  visibility: hidden;
  opacity: 0;
  -webkit-box-shadow: 1px 1px 5px 0px rgba(22, 23, 22, 0.5);
  -moz-box-shadow: 1px 1px 5px 0px rgba(22, 23, 22, 0.5);
  box-shadow: 1px 1px 5px 0px rgba(22, 23, 22, 0.5);
  transition: All .3s ease;
  -webkit-transition: All .3s ease;
  -moz-transition: All .3s ease;
  -o-transition: All .3s ease;
}
.hover-pane:hover .hover-action-link {
  visibility: visible;
  opacity: 0.8;
}
.hover-pane .hover-action-link:hover {
  opacity: 1;
}
